Vue.config.productionTip = false

import './assets/app.css'

/* eslint-disable no-new */
var vm = new Vue({
  store,
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  created(){
    this.$store.state.estas = router.currentRoute.path
  },
})


router.beforeEach((to, from, next) => {
  vm.$store.state.estas = to.path
  next()
})
