<template lang="html">
  <div class="contenedorx">
    <div class="color"></div>
    <titlex :titlex="titlex" :textx="textx"/>
    <div class="con-codes">
      <slot name="codes">
      </slot>
      <div v-if="apix" class="apis">
        <api :data="apix">
        </api>
      </div>
    </div>

  </div>

</template>

<script>
import titlex from './title.vue'
import codex from './code.vue'
import api from './api.vue'
import $ from 'jquery'
export default {
  props:[
    'titlex',
    'textx',
    'apix'
  ],
  components:{
    titlex,
    codex,
    api,
  },
  data(){
    return {
      
    }
  },
  mounted(){
    var lastScrollTop = 0;

    $('.con-codes').bind('mousewheel', function(e){
      // console.log($(".con-codes").scrollTop());
            if(e.originalEvent.wheelDelta /120 > 0) {
                // console.log('scrolling up !');
                if($(".con-codes").scrollTop()==0){
                  $(".con-codes").css({"transform":'translate(0,45px)'});
                  setTimeout(function () {
                    $(".con-codes").css({"transform":'translate(0,0px)'});
                  }, 300);
                }
            }
            else{
              if(($(".con-codes")[0].scrollHeight - $(".con-codes")[0].scrollTop) === $(".con-codes")[0].clientHeight){
                $(".con-codes").css({"transform":'translate(0,-110px)'});
                setTimeout(function () {
                  $(".con-codes").css({"transform":'translate(0,-55px)'});
                }, 300);
              }
                // console.log('scrolling down !');
            }
        });

$(".con-codes").scroll(function(event){
   var st = event.target.scrollTop
   if (st > lastScrollTop){
     if(st>500){
        $('.con-titulo').css({"transform":'translate(0,-55px)'});
        $('.con-codes').css({"transform":'translate(0,-55px)'});
     }
     // console.log('para abajo');
   } else {
     if(st>500){
       $('.con-codes').css({"transform":'translate(0,0px)'});
       $('.con-titulo').css({"transform":'translate(0,0px)'});
     }
     // console.log('para arriba');
   }
   lastScrollTop = st;
});
    $(".con-codes").scroll(function(event) {
      if($(this).scrollTop())
      $('.contenedorx').find(".color").css({'top':'-'+$(this).scrollTop()/3+'px'});
      if($(this).scrollTop()>400){
        $('.contiene-titulo').addClass('solo-title');
      } else {

        $('.contiene-titulo').removeClass('solo-title');
      }
    });
  }
}
</script>

<style lang="css" scoped>
.contenedorx::after {
    position: absolute;
    bottom: 0px;
    content: '';
    left: 0px;
    width: 100%;
    height: 150px;
    background: linear-gradient(bottom,rgb(245, 245, 245) 0%, rgba(245, 245, 245,.0) 100%);
    z-index: 7000;
}
.color {
  background: linear-gradient(45deg, rgba(var(--morado),1) 0%,rgb(198, 45, 191) 50%, rgb(250, 178, 97) 100%);
  padding-bottom: 30px;
  width: 105%;
  height: 400px;
  position: absolute;
  left: -65px;
  top: 0px;
  /* top: -110px; */
  display: block;
  /* transform: translate(0,-200px); */
  z-index: 100;
  transform: rotate(-4deg) translate(0,-100px);
  border-radius: 100px;
  box-shadow: 0px 0px 30px 0px rgba(var(--morado),.5);
  overflow: hidden;
}
.color::after {
  position: absolute;
  top: 0px;
  left: 100px;
  content: '';
  width: 0px;
  height: 0px;
  box-shadow: inset 0px 0px 20px 0px rgb(255, 255, 255);
  border-radius: 50%;
  animation: gota 6s ease infinite;
  display: block;
}
@keyframes gota {
	0% {
		width: 0px;
    height:0px;
	}
	60% {
    opacity: 0;
    left: -250px;
    top: -250px;
    height:800px;
		width: 800px;
	}
	100% {
    opacity: 0;
    left: -250px;
    top: -250px;
    height:800px;
		width: 800px;
	}
}
.contenedorx {
  width: 100%;
  /* overflow: auto; */
  height: 100vh;
  position: absolute;
}
.con-codes {
  width: calc(100%);
  /* max-width: 1060px; */
  z-index: 5000;
  /* padding: 30px; */
  margin: auto;
  /* background: rgb(129, 227, 70); */
  position: relative;
  /* margin-top: 10px; */
  /* display: flex; */
  /* align-items: center;
  justify-content: flex-start;
  flex-direction: column; */
  overflow: auto;
  height: 100%;
  padding-bottom: 250px;
  transition: all .3s ease;
}
</style>



