<template lang="html">
  <div class="menu-doc">
    <div @click="$router.push('/')" class="img-menu">
      <img src="../assets/vuesax-logo-vertical-2.png" alt="">
    </div>
    <ul class="ul-principal">
      <li v-if="true">Vuesax</li>
      <ul v-if="true" class="ul-hijo">
        <li>
          <router-link to="/docs/getting-started" class="nav-link" active-class="activo">
            Getting Started
          </router-link>
        </li>
        <li v-if="false">
          <router-link to="/docsx/" class="nav-link" active-class="activo">
            ¿Por que Vuesax?
          </router-link>
        </li>
        <li v-if="false">
          <router-link to="/docsx/" class="nav-link" active-class="activo">
            Preguntas Frecuentes
          </router-link>
        </li>
        <li v-if="false">
          <router-link to="/docsx/" class="nav-link" active-class="activo">
            Patrocinadores
          </router-link>
        </li>
        <li v-if="false">
          <router-link to="/docsx/" class="nav-link" active-class="activo">
            Contribuyendo
          </router-link>
        </li>
      </ul>
      <li>Components</li>
      <ul class="ul-hijo">
        <li>
          <router-link to="/docs/buttons" class="nav-link" active-class="activo">
            buttons
          </router-link>
        </li>
        <!-- <li>
          <router-link to="/docs/selects" class="nav-link" active-class="activo">
            selects
          </router-link>
        </li> -->
      </ul>
    </ul>
  </div>
</template>

<script>
export default {
  methods:{
    pushx(ruta){
      console.log(ruta);
      var _this = this
      this.$router.push(ruta,function(){
        _this.$set(_this.$store.state,'estas',ruta)
      })
    }
  }
}
</script>

<style lang="css" scoped>
a {
  color: rgba(0, 0, 0, 0.6);
  display: block;
}
.activo {
  color: rgb(var(--morado));
}
  .ul-principal {
    /* border-bottom: 1px solid rgba(0, 0, 0, 0.040); */
  }
  li {
    cursor: pointer;
    color: rgba(0, 0, 0, 0.6);
    transition: all .3s ease;
  }
  .ul-principal>li{
    padding: 15px;
    font-size: 17px;
    font-weight: bold;
    border-bottom: 1px solid rgba(0, 0, 0,.040);
    border-left: 0px solid rgb(var(--morado));
  }
  .ul-principal>li:hover {
    color: rgb(var(--morado));
    border-left: 4px solid rgb(var(--morado));
  }
  .ul-hijo {
    border-bottom: 1px solid rgba(0, 0, 0,.040);

  }
  .ul-hijo li a{
    padding: 9px;
    padding-left: 25px;
  }
  .ul-hijo li:last-child a{
    border-bottom: 0px solid rgba(0, 0, 0,.040) !important;
  }
    .ul-hijo li:hover a{
      color: rgb(var(--morado));
    }
  .menu-doc {
      position: fixed;
      left: 0px;
      top: 0px;
      height: 100vh;
      width: 260px;
      background: rgb(255, 255, 255);
      z-index: 10000;
      box-shadow: 10px 0px 20px 0px rgba(0, 0, 0,.030);
  }
  .img-menu {
    cursor: pointer;
      width: 100%;
      border-bottom: 1px solid rgba(0, 0, 0, 0.05);
      padding: 30px;
      padding-top: 15px;
      padding-bottom: 15px;
      transition: all .3s ease;
  }
  .img-menu:hover img{
      transform: scale(1.040);
  }
  .img-menu img {
    backface-visibility: hidden;
    width: 100%;
    transition: all .3s ease;
  }
</style>



