<template lang="html">
  <div class="con-code">
    <header>
      <h2>{{titlex}}</h2>
      <ul v-if="!doc" class="ul-code">
        <li :class="{'codeActivo':codeActivo}" @click="codeActivo=!codeActivo,colorearCode()" class="flaticon-coding"></li>
        <li class="flaticon-3d-outlined-shape"></li>
        <li class="flaticon-github-logo"></li>
      </ul>
    </header>

    <div class="con-ejemplo">
      <slot name="ejemplo">

      </slot>
    </div>
    <div v-if="codeActivo||doc" class="code">
      <pre><code class="html">{{code}}</code></pre>
    </div>
  </div>
</template>

<script>

import $ from 'jquery'
import '../assets/font/flaticon.css'
var hljs = require('highlight.js');
export default {
  props:[
    'titlex',
    'code',
    'doc'
  ],
  data(){
    return {
      codeActivo:false,
    }
  },
  mounted(){
    $('pre code').each(function(i, block) {
    hljs.highlightBlock(block);
    });
  },
  methods:{
    colorearCode(){
      setTimeout(function () {
        $('pre code').each(function(i, block) {
          hljs.highlightBlock(block);
        });

      }, 1);
    }
  }
}
</script>

<style lang="css">
.codeActivo {
  background: rgb(255, 255, 255);
  box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.1);
  transform: translate(0,-3px);
  color: rgb(var(--morado)) !important;
}
.code {
  padding: 10px;
}
*::before {
    margin-left: 0px !important;
}
.ul-code {
  position: relative;
  display: flex;
  align-items: center;
}

.ul-code li {
  padding: 9px;
  text-align: center;
  border-radius: 5px;
  cursor: pointer;
  color: rgba(0, 0, 0, 0.7);
  transition: all .3s ease;
  margin-left: 7px;
}
.ul-code li:hover {
  background: rgb(255, 255, 255);
  box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.1);
  transform: translate(0,-3px);
  color: rgb(var(--morado));
}
.con-ejemplo>div{
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
  h2 {
    color: rgba(0, 0, 0, 0.7);
    font-weight: lighter;
  }
  header {
    width: 100%;
    padding: 10px;
    background: rgb(245, 245, 245);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 20px;
  }
  .con-code {
    width: 100%;
    max-width: 1000px;
    margin: auto;
    background: rgb(255, 255, 255);
    position: relative;
    z-index: 1000;
    display: block;
    border-radius: 5px;
    overflow: hidden;
    /* box-shadow: 0px 5px 40px 0px rgba(0, 0, 0, 0.10); */
    box-shadow: 0px 20px 40px -15px rgba(0, 0, 0, 0.15);
    margin-top: 20px;
    transition: all .3s ease;
    display: block;
    backface-visibility: hidden;
  }
  .hljs-name {
    color: rgba(0, 0, 0,.7);
  }
  .hljs-string {
    color: rgb(227, 122, 141) !important;
  }
  .hljs-attr {
   color:rgb(102, 57, 193)

  }
  .code {
    /* font-style: italic; */
    font-weight: normal;
    padding: 25px;
    background: rgb(245, 245, 245);
    color: rgba(0, 0, 0,.5);
  }
  .code span {
    letter-spacing: 1px;
    line-height: 21px;
  }


</style>



