<template lang="html">
  <div class="con-doc">
    <menu-doc/>
  <div class="doc">
    <transition name="fade">
    <router-view/>
  </transition>
  </div>
  </div>
</template>

<script>
import menuDoc from '../components/menuDoc.vue'
export default {
  components:{
    menuDoc
  }
}
</script>

<style lang="css" scoped>
.fade-enter-active, .fade-leave-active {
  transition: all .5s;
}
.fade-enter  /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translate(-100%) scale(0.7);
}
.fade-leave-to {
  opacity: 0;
  transform: translate(100%) scale(0.7);
}
.con-doc {
  margin-left: 260px;
  top: 0px;
  /* padding: 30px; */
  padding-top: 0px;
  left: 0px;
  width: calc(100% - 260px);
  height: 100%;
  position: fixed;
  background: rgb(245, 245, 245);
  /* background: linear-gradient(45deg, rgba(var(--morado),1) 0%, rgba(var(--morado),1) 100%); */
  z-index: 1000;

}
</style>



