<template lang="html">
  <div class="con-api">
    <h2>API</h2>
    <table>
      <thead>
        <tr>
          <th>Name</th>
          <th>Type</th>
          <th>Descripción</th>
          <th>default</th>
          <th>Ejemplos</th>
        </tr>
      </thead>
      <tbody v-for="tr in data">
        <tr>
          <td>{{tr.name}}</td>
          <td>{{tr.type}}</td>
          <td>{{tr.descripcion}}</td>
          <td>{{tr.default}}</td>
          <td class="btn-ejemplo"><button type="button" name="button">Ejemplo</button></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  props:[
    "data"
  ]
}
</script>

<style lang="css" scoped>
.h3-ejemplo {
  padding: 20px;
  font-weight: lighter;
}
.btn-ejemplo button{
  padding: 10px;
}
.btn-ejemplo{
  padding: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.con-api {
  width: 100%;
  max-width: 1000px;
  background: rgb(255, 255, 255);
  box-shadow: 0px 20px 40px -15px rgba(0, 0, 0, 0.15);
  margin: auto;
  margin-top: 60px;
  padding: 10px;
  border-radius: 5px;
}
h2  {
  color: rgb(var(--morado));
  padding: 15px;
  /* border-bottom: 1px solid rgba(0, 0, 0, 0.050); */
}
table {
    width: 100%;
    border-collapse: collapse;
}
table td {
  padding: 10px;
  border: 1px solid rgba(0, 0, 0, 0.050);
  color: rgba(0, 0, 0, 0.7);
}
th {
  padding: 10px;
  text-align: left;
  border: 1px solid rgba(0, 0, 0, 0.050);
  /* color: rgb(var(--morado)); */
  font-weight: lighter;
  background: rgb(250, 250, 250);
}
tbody tr{
  border-bottom: 1px solid rgba(0, 0, 0, 0.050);
}
</style>



