<template lang="html">
  <div class="contiene-titulo">
    <div class="con-titulo">
      <h2>{{titlex}}</h2>
      <h3>{{textx}}</h3>
    </div>
  </div>
</template>

<script>

export default {
  props:[
    "titlex",
    "textx"
  ]
}
</script>

<style lang="css" scoped>

.contiene-titulo {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  z-index: 100;
 }

  .con-titulo {
    position: relative;
    width: calc(100% - 60px);
    margin-top: 0px;
    background: rgb(255, 255, 255);
    padding: 10px;
    border-radius: 0px 0px 15px 15px;
    color: rgba(0, 0, 0, 0.6);
    box-shadow: 0px 5px 40px 0px rgba(0, 0, 0, 0.1);
    z-index: 6000;
    max-width: 1050px;
    transition: all .3s ease;
    top: 0px;
  }
  .con-titulo h2 {
      padding: 10px;
      text-align: center;
      font-size: 40px;
      font-weight: lighter;
      color: rgba(var(--morado),1);
      border-bottom: 1px solid rgba(0, 0, 0, 0.050);
      transition: all .3s ease;
  }
  .con-titulo h3 {
    text-align: center;
    font-size: 17px;
    font-weight: lighter;
    padding: 10px;
    transition: all .3s ease;
  }
  .solo-title {
    z-index: 6000;
    position: relative;
  }
  .solo-title .con-titulo{
    width: 100% !important;
    max-width: 100%;
    border-radius: 0px;
  }
  .solo-title h2{
    padding-top: 0px;
    padding-bottom: 0px;
    border-bottom: 1px solid rgba(0, 0, 0, 0);
    font-size: 30px;
  }
  .solo-title h3 {
    opacity: 0;
    height: 0px;
    overflow: hidden;
    padding-top: 0px;
    padding-bottom: 0px;
  }
</style>



